import {
	mapState
} from 'vuex';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待确认',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '已确认',
					loadingType: 'more',
					orderList: []
				}, {
					state: 3,
					text: '售后中',
					loadingType: 'more',
					orderList: []
				}
			],
			ljOrderList: [],
			seeMoreFlag: false
		};
	},
	computed: {
		...mapState(['token'])
	},
	onLoad(options) {
		var _self = this;
		_self.tabCurrentIndex = options.state;
		uni.showLoading({
			title: '加载中...',
		});
		_self.getljOrderList();
		if (_self.ljOrderList.length >= 1) {
			setTimeout(() => {
				uni.hideLoading();
			}, 1000)
			// }
		}

	},
	onShow() {
		var _self = this;
		uni.showLoading({
			title: '加载中...',
		});
		if (_self.navList[0].orderList.length <= 1) {
			_self.tabCurrentIndex = _self.tabCurrentIndex;
			_self.loadData('tabChange');
			if (_self.ljOrderList.length >= 1) {
				_self.loadData();
			}
			setTimeout(() => {
				uni.hideLoading();
			}, 1000)

		}
		_self.changeTabFade(_self.tabCurrentIndex)
	},
	onPullDownRefresh() {
		var _self = this;
		_self.ljOrderList = [];
		_self.getljOrderList();
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 500)
	},
	methods: {
		// 解决蒙层滚动问题
		moveHandle() {
			return;
		},
		hideModalMore() {
			this.seeMoreFlag = false;
		},
		seeMore() {
			this.seeMoreFlag = true;
		},
		//获取订单列表
		getljOrderList() {
			var _self = this;
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/orders/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					_self.check_statusCode(res.statusCode);
					res.data.forEach(item => {
						var temp_url = item.url;
						uni.request({
							url: temp_url,
							header: {
								'Authorization': ' Token ' + now_token
							},
							method: "GET",
							success: (res) => {
								var tempGoodList = []
								_self.check_statusCode(res.statusCode)
								// 封装时间 // 封装总价，总数量
								try {
									var tempData = {
										time: res.data.order_create_time.substring(0, 10) + ' ' + res.data.order_create_time.substring(11,
											16),
										count_num: res.data.count_num,
										count_price: res.data.count_price,
										deliver_type: res.data.deliver_type,
										deliver_com: res.data.deliver_com,
										deliver_num: res.data.deliver_num,
										user_note: res.data.user_note,
										id: res.data.id,
										address_info: res.data.buy_address[0],
									}
									// 封装状态
									var temp_state = 0;
									switch (res.data.order_status) {
										case '待确认':
											temp_state = 1;
											break;
										case '已确认':
											temp_state = 2;
											break;
										case '售后中':
											temp_state = 3;
											break;
									}
									tempData.state = temp_state;
									// 封装商品列表
									// 只有一个商品时候，多个同时用
									res.data.buy_good.forEach(item => {
										var tempGoodListItem = {
											title: item.name,
											price: item.price,
											image: item.picture,
											number: item.goods_num,
											attr: item.value
										}
										tempGoodList.push(tempGoodListItem);
									})
									tempData.goodsList = tempGoodList;
									_self.ljOrderList.push(tempData)
								} catch (e) {
									uni.showModal({
										title: '提示',
										content: '未知错误，请重新操作！',
										showCancel: false,
										success: function(res) {
											if (res.confirm) {
												uni.redirectTo({
													url: '/pages/index/index'
												})
											}
										}
									});
								}
							}
						});
					});
				}
			});
			// _self.loadData();
		},
		loadData(source) {
			this.navList = [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待确认',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '已确认',
					loadingType: 'more',
					orderList: []
				}, {
					state: 3,
					text: '售后中',
					loadingType: 'more',
					orderList: []
				}
			];
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}
			navItem.loadingType = 'loading';
			// setTimeout(() => {
			// 过滤器
			let orderList = this.ljOrderList.filter(item => {
				//添加不同状态下订单的表现形式
				item = Object.assign(item, this.orderStateExp(item.state));
				// 演示数据所以自己进行状态筛选
				if (state === 0) {
					//0为全部订单
					return item;
				}
				return item.state === state
			});
			orderList.forEach(item => {
				navItem.orderList.push(item);
			})
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);
			// 判断是否还有数据， 有改为 more， 没有改为noMore 
			navItem.loadingType = 'noMore';
			setTimeout(() => {
				uni.hideLoading();
			}, 500);

		},
		changeTabFade(e) {
			var tempIndex = e + 1 - 1;
			this.tabCurrentIndex = tempIndex;
		},
		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index, do_what) {
			var that = this;
			var now_token = this.token
			var text_content = "";
			if (do_what == 'delete') {
				text_content = '您确定删除该订单吗'
			} else {
				text_content = '您确定取消该交易吗'
			}
			uni.showModal({
				title: '提示',
				content: text_content,
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '请稍后'
						})
						setTimeout(() => {
							uni.request({
								url: that.global_baseUrl + '/orders/' + index,
								header: {
									'Authorization': ' Token ' + now_token
								},
								method: "DELETE",
								success: (res) => {
									that.check_statusCode(res.statusCode);
									uni.showToast({
										icon: 'success',
										title: '操作成功！'
									}, 500);
									this.ljOrderList = [];
									that.getljOrderList();
								}
							});
							uni.hideLoading();
						}, 600)
					} else if (res.cancel) {

					}
				}
			});

		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待确认';
					break;
				case 2:
					stateTip = '已确认';
					break;
				case 3:
					stateTip = '售后中';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
